	import { mapGetters } from 'vuex';
	import GameInfoSelection from './GameInfoSelection';
	import groupBy from 'lodash/groupBy';
	import sortBy from 'lodash/sortBy';

	export default {
		data(){
			let vm = this;

	        return {
	        	"originComs": [],
	        	"comms": { "2d":[], "3d":[], "4d":[], "da":[] },
	        	"header": { 
	        		"1": {
	        			"2d": [{"id":1, "value": "Đầu"},{"id":2, "value": "Đuôi"},{"id":4, "value": "27 Lô"}],
	        			"3d": [{"id":6, "value": "Đầu"},{"id":7, "value": "Đầu"},{"id":9, "value": "23 Lô"}],
	        			"4d": [{"id":11, "value": "Đuôi"},{"id":12, "value": "20 Lô"}],
	        			"da": [{"id":14, "value": "Đá thẳng"}]
	        		}, 
	        		"2": {
	        			"2d": [{"id":1, "value": "Đầu"},{"id":2, "value": "Đuôi"},{"id":3, "value": "7 Lô"},{"id":5, "value": "18 Lô"}],
	        			"3d": [{"id":6, "value": "Đầu"},{"id":7, "value": "Đuôi"},{"id":8, "value": "7 Lô"},{"id":10, "value": "17 Lô"}],
	        			"4d": [{"id":11, "value": "Đuôi"},{"id":13, "value": "16 Lô"}],
	        			"da": [{"id":14, "value": "Đá thẳng"},{"id":17, "value": "Đá xiên"}]
	        		}, 
	        		"4": {
	        			"2d": [{"id":1, "value": "Đầu"},{"id":2, "value": "Đuôi"},{"id":3, "value": "7 Lô"},{"id":5, "value": "18 Lô"}],
	        			"3d": [{"id":6, "value": "Đầu"},{"id":7, "value": "Đuôi"},{"id":8, "value": "7 Lô"},{"id":10, "value": "17 Lô"}],
	        			"4d": [{"id":11, "value": "Đuôi"},{"id":13, "value": "16 Lô"}],
	        			"da": [{"id":14, "value": "Đá thẳng"}]
	        		},
				},
				"currentGame": "1",
				"propFilter": ["2d", "3d", "4d", "da"],
				"channelsToday": [],
				"numberSearch": "",

	            "today": moment().format('DD/MM/Y'),
	            "displayTangCoForm": false,
	            "tangCoFormTabs": 1
	        }
	    },

	    mounted(){
	    	this.initScript();
	    	let channelsToday = this.getchannelsToday();
	    	$("#filterChannelSelection").select2({data: channelsToday, width:"100%"});
	    },

	    watch:{
	    	originComs: function(comms){
	    		this.comms = this.groupCommByProp(comms);
	    		if($("#filterChannelSelection").val() == ""){
	    			$("#filterChannelSelection").val(parseInt($("#filterChannelSelection option:eq(0)").val())).trigger('change.select2');
	    		}else{
	    			$("#filterChannelSelection").trigger('change.select2');
	    		}
	    	},
	    	numberSearch: function(n){
	    		this.filterByNumber(n);
	    	},
	    	// displayTangCoForm: async function(display){
	    	// 	if(!display) return;
	    	// 	await this.$nextTick(); // wait for DOM to update

	    	// 	let vm = this;
	    	// }
	    },

	    components: {
        	'game-info-selection': GameInfoSelection
        },

	    created(){
    		this.getIncrCommission();
    	},

    	computed: {
			...mapGetters({
				user: 'user',
		  		gameInfo: 'gameInfo'
			})
		},

	    methods: {
	    	initScript: function(){
	    		let vm = this;

				$("#filterChannelSelection").on("change.select2", function(e){
					if(!$(this).select2('data')[0]) return;

					let game = $(this).select2('data')[0].gameId;
					let channel = $(this).val();
					vm.currentGame = parseInt(game);
					let data = vm.originComs.filter(function(el){
						let elc = el.channel.split(".");
						return elc.includes(channel);
					});
					vm.comms = vm.groupCommByProp(data);
				});
	    	},

	    	getchannelsToday: function() {
	    		let vm = this;
				let ccc = [];
				for(let game in this.gameInfo.channelToday){
					if(game == 3) continue;

					let cs = this.gameInfo.channelToday[game].map((el) => {
        				return { id: el.channel_id, text: vm.gameInfo.channels[parseInt(el.channel_id)].alias, gameId: game };
        			});

        			ccc = ccc.concat(cs);
				}

				return ccc;
			},

			channelToName: function(channel){
				let vm = this;
				if(channel.indexOf(".") >= 0){
					let chns = channel.split(".").map((cns) => { return vm.gameInfo.channels[parseInt(cns)].alias; });
        			return chns.join(", ");
				}

				return vm.gameInfo.channels[parseInt(channel)].alias;
			},

	    	propGroupToName: function(propId){
	    		propId = parseInt(propId);

	    		switch(propId){
	    			case 1:
	    			case 2:
	    			case 3:
	    			case 4:
	    			case 5:
	    				return "2d";
	    			case 6:
	    			case 7:
	    			case 8:
	    			case 9:
	    			case 10:
	    				return "3d";
	    			case 11:
	    			case 12:
	    			case 13:
	    				return "4d";
	    			case 14:
	    			case 15:
	    			case 16:
	    				return "da";
	    			case 17:
	    			case 18:
	    			case 19:
	    				return "da";
	    		}

	    		return "";
	    	},

	    	groupCommByProp: function(comms){
	    		let commission = {};
	    		let vm = this;

	    		comms = groupBy(comms, function(val){
	    			return vm.propGroupToName(val.prop);
	    		});

	    		for(let prop in comms){
	    			if(!commission[prop]) commission[prop] = [];

	    			let gcns = groupBy(comms[prop], function(val){ return val.game+"_"+val.channel+"_"+val.number+"_"+val.code; });
	    			for(let gcn in gcns){
	    				let number = gcn.split("_")[2];
	    				let propComm = groupBy(gcns[gcn], function(val){ return val.prop; });

    					commission[prop].push({
		    				"number": number,
		    				"comm": propComm
		    			});
	    			}
	    		}

	    		for(let prop in commission){
	    			let arr = sortBy(commission[prop], function(o) { return o.number; })
	    			commission[prop] = arr;
	    		}

	    		return commission;
	    	},

	        getIncrCommission: function(){
	        	let vm = this;

	        	window.axios.get("admin-panel/tools/commission")
	        				.then((res) => {
	        					res = res.data;
	        					vm.originComs = res.data;
	        				})
	        				.catch((error) => {

	        				});
	        },

	        generateChannelToday: function(game){
	        	let vm = this;
	        	let data = [];

	        	if(game == 3){
        			data = vm.gameInfo.channelToday[game].map((el) => {
        				let chns = el.channel_id.split(".").map((cns) => { return vm.gameInfo.channels[parseInt(cns)].alias; });
        				return { id: el.channel_id, text: chns.join(", ") };
        			});
        		}else{
        			data = vm.gameInfo.channelToday[game].map((el) => {
        				return { id: el.channel_id, text: vm.gameInfo.channels[parseInt(el.channel_id)].alias };
        			});
        		}

        		data.unshift({"id": "", "text": ""});
        		return data;
	        },

	        filterByNumber: function(val){
	        	let vm = this;
	        	let channel = $("#filterChannelSelection").val();

	        	let filterd = this.originComs.filter(function(el){
	        		let elc = el.channel.split(".");
					return elc.includes(channel);
	        	});

	        	if(val == "") {
					vm.comms = vm.groupCommByProp(filterd);
	        		return;
	        	}

	        	filterd = filterd.filter(function(el){
	        		if(el.number.includes(".")){
	        			return el.number.includes(val);
	        		}

	        		return el.number == val;
	        	});

	        	vm.comms = vm.groupCommByProp(filterd);
	        },

	        showTangCoForm: function() {
	        	let vm = this;

	        	this.displayTangCoForm = true;

	        	$("#tangcoForm").on('hidden.bs.modal', function () {
	        		vm.displayTangCoForm = false;
	        		vm.tangCoFormTabs = 1;
				});
	        	$("#tangcoForm").modal('show');
	        	
            	$(".game-select").select2({placeholder:"Miền"});
				$(".channel-select").select2({placeholder:"Đài"});
				$(".prop-select").select2({placeholder:"Kiểu"});
	        },

	        incrCommission: function(){
	        	let vm = this;

        		let data = [];
        		let forms = $(".tab-pane form");
        		for(let i=0;i<forms.length;i++){
        			let incrValue = $(forms[i]).find(".incrValue").val();
        			let channel = $(forms[i]).find(".channelSelection").val();
        			let prop = $(forms[i]).find(".propSelection").val();
        			let game = $(forms[i]).find(".gameSelection").val();
        			let number = $(forms[i]).find(".number").val();
        			let code = $(forms[i]).find(".subCode").val();

        			if(number == ""){
        				swal("Lỗi", "Số không được bỏ trống!", "error");
        				return;
        			}

        			if(game == ""){
        				swal("Lỗi", "Vui lòng chọn game!", "error");
        				return;
        			}

        			if(incrValue == ""){
        				swal("Lỗi", "Mục nhập cò tăng không được để trống!", "error");
        				return;
        			}

        			if(code.indexOf(vm.user.code) < 0){
        				swal("Lỗi", "Không thể tăng cò cho cấp dưới " + code, "error");
        				return;
        			}

        			if((code.length - vm.user.code.length !== 2) && (code.length - vm.user.code.length !== 0)){
        				swal("Lỗi", "Không thể tăng cò cho cấp dưới " + code, "error");
        				return;
        			}

        			data.push({
        				game: parseInt(game),
        				channel: channel.join(","),
        				prop: prop.join(","),
        				number: number,
        				code: code,
        				incrValue: parseFloat(incrValue)
        			});
        		}

        		window.axios.post("admin-panel/tools/commission", { "commission": data })
			    .then((response) => {
			    	if(response.data.code == 0){
			    		swal("", "Tăng cò thành công", "success");
			    		vm.getIncrCommission();
			    		$("#tangcoForm").modal('hide');
			    	}
			    })
			    .catch((error) => { });
	        },
	        onMoreCommTab: function(){
	        	this.tangCoFormTabs++;
	        }
	    }
	}

</script>

