<template>
	<div class="m-portlet m-portlet--brand betted-ratio">
		<div class="m-portlet__head row">
			<div class="col-12 col-sm-3">
				<input type='text' class="form-control date_range_picker" id="datepicker_bettedratio" readonly placeholder="Chọn ngày" type="text" />
			</div>
			<div class="col-12 col-sm-4">
				<div class="form-group m--margin-top-10">
					<div class="m-checkbox-inline">
						<label class="m-checkbox">
							<input type="checkbox" value="1" v-model="gameFilter"> Miền Bắc
							<span></span>
						</label>
						<label class="m-checkbox">
							<input type="checkbox" value="4" v-model="gameFilter"> Miền Trung
							<span></span>
						</label>
						<label class="m-checkbox">
							<input type="checkbox" value="2" v-model="gameFilter"> Miền Nam
							<span></span>
						</label>
						<label class="m-checkbox">
							<input type="checkbox" value="3" v-model="gameFilter"> Xiên MN (18A+18B)
							<span></span>
						</label>
					</div>
				</div>
			</div>
			<div class="col-12 col-sm-5">
				<button @click="this.getBettedRatioData" type="button" class="btn btn-brand m-btn m-btn--custom">Xem</button>
			</div>
		</div>
		<div class="m-portlet__body">
			<!--begin::Section-->
			<div class="m-section">
				<div class="m-section__content">
					<data-table 
						tableId="bet-ratio"
						:header="this.tableHeader"
						:columns="this.tableColumns"
						:columnDefs="this.columnDefs"
						:columnSearch="this.columnSearch"
						:data="this.data"
					/>
				</div>
			</div>
			<!--end::Section-->
		</div>
	</div>
</template>

<script>
	import moment from 'moment';
	import { mapGetters } from 'vuex';
	import DataTable from "../DataTable";

	export default {
		data(){
			let vm = this;

	        return {
	        	"tableHeader": ["Miền","Đài","Kiểu","Number","Tổng điểm","Giới hạn","Tỷ lệ (%)"],
	        	"columnSearch": [{pos:0}, {pos:1}, {pos:2}, {pos:3}, {pos:4}, {pos:5}, {pos:6}],
	        	"tableColumns": [{data: 'game'},{data: 'channel'},{data: 'prop'},{data: 'number'},{data: 'points'},{data: 'limit'},{data: 'limit'}],
	        	"data": [],
	        	"today": moment().format('DD/MM/Y'),
	        	"gameFilter": [1,2,3,4],
	        	"columnDefs": [
					{
						targets: 0,
						render: function(data, type, full, meta) {
							let g = vm.gameInfo.games[data];
							return g;
						},
					},
					{
						targets: 1,
						render: function(data, type, full, meta) {
							if(data == "") return "Tất cả";
							let channels = [];
							for(let i=0;i<data.length;i++){
								channels.push(vm.gameInfo.channels[data[i]].alias);
							}
							
							return channels.join(", ");
						},
					},
					{
						targets: 2,
						render: function(data, type, full, meta) {
							let p = vm.gameInfo.props[data];
							return p;
						},
					},
					{
						targets: 5,
						orderable: true
					},
					{
						targets: 6,
						orderable: true,
						render: function(data, type, full, meta) {
							let points = parseInt(full.points);
							let limit = parseInt(full.limit);
							return ((points/limit*1.0)*100).toFixed(2);
						},
					}
				]
	        }
	    },

	    components: {
        	'data-table': DataTable
        },

        mounted(){
        	this.initDatePicker();
        },

    	computed: {
			...mapGetters({
				user: 'user',
		  		gameInfo: 'gameInfo'
			})
		},

	    methods: {
	        getBettedRatioData: function(){
	        	let vm = this;
	        	let game = vm.gameFilter;
	        	let date = $("#datepicker_bettedratio").val();

	        	window.axios.get("admin-panel/tools/betted-ratio?game="+game+"&date="+date)
    				.then((res) => {
    					res = res.data;
    					vm.data = res.data;
    				})
    				.catch((error) => {

    				});
	        },
	        initDatePicker: function(){
	        	$('#datepicker_bettedratio').daterangepicker({
					singleDatePicker: true,
					showDropdowns: true,
					minYear: 2019,
					locale: {
				    	format: 'YYYY-MM-DD'
				    },
					maxYear: parseInt(moment().format('YYYY'),2)
				}, function(start, end, label) {
					// var years = moment().diff(start, 'years');
					// alert("You are " + years + " years old!");
				});
	        }
	    }
	}

</script>
<style>
	.betted-ratio .m-portlet__head.row{
		padding: 0;
		padding-top: 10px;
	}
</style>